/**
 * Toto Design System - Spacing Tokens
 * Unified spacing system for the entire Toto ecosystem
 */

export const spacing = {
  // Base spacing scale (4px base unit)
  0: '0',
  px: '1px',
  0.5: '0.125rem',  // 2px
  1: '0.25rem',     // 4px
  1.5: '0.375rem',  // 6px
  2: '0.5rem',      // 8px
  2.5: '0.625rem',  // 10px
  3: '0.75rem',     // 12px
  3.5: '0.875rem',  // 14px
  4: '1rem',        // 16px
  5: '1.25rem',     // 20px
  6: '1.5rem',      // 24px
  7: '1.75rem',     // 28px
  8: '2rem',        // 32px
  9: '2.25rem',     // 36px
  10: '2.5rem',     // 40px
  11: '2.75rem',    // 44px
  12: '3rem',       // 48px
  14: '3.5rem',     // 56px
  16: '4rem',       // 64px
  20: '5rem',       // 80px
  24: '6rem',       // 96px
  28: '7rem',       // 112px
  32: '8rem',       // 128px
  36: '9rem',       // 144px
  40: '10rem',      // 160px
  44: '11rem',      // 176px
  48: '12rem',      // 192px
  52: '13rem',      // 208px
  56: '14rem',      // 224px
  60: '15rem',      // 240px
  64: '16rem',      // 256px
  72: '18rem',      // 288px
  80: '20rem',      // 320px
  96: '24rem',      // 384px

  // Legacy spacing for compatibility
  xxs: '0.25rem',   // 4px
  xs: '0.5rem',     // 8px
  sm: '1rem',       // 16px
  md: '1.5rem',     // 24px
  lg: '2rem',       // 32px
  xl: '2.5rem',     // 40px
} as const;

// Semantic spacing tokens
export const semanticSpacing = {
  // Component spacing
  component: {
    padding: {
      xs: spacing[2],    // 8px
      sm: spacing[3],    // 12px
      md: spacing[4],    // 16px
      lg: spacing[6],    // 24px
      xl: spacing[8],    // 32px
    },
    margin: {
      xs: spacing[2],    // 8px
      sm: spacing[3],    // 12px
      md: spacing[4],    // 16px
      lg: spacing[6],    // 24px
      xl: spacing[8],    // 32px
    },
    gap: {
      xs: spacing[1],    // 4px
      sm: spacing[2],    // 8px
      md: spacing[3],    // 12px
      lg: spacing[4],    // 16px
      xl: spacing[6],    // 24px
    },
  },

  // Layout spacing
  layout: {
    container: {
      padding: spacing[4],     // 16px
      maxWidth: '1200px',
    },
    section: {
      padding: spacing[16],    // 64px
      margin: spacing[8],      // 32px
    },
    grid: {
      gap: spacing[6],         // 24px
    },
  },

  // Form spacing
  form: {
    field: {
      marginBottom: spacing[4], // 16px
    },
    label: {
      marginBottom: spacing[1], // 4px
    },
    error: {
      marginTop: spacing[1],    // 4px
    },
  },
} as const;

// CSS Custom Properties for runtime usage
export const cssVariables = {
  '--toto-spacing-xs': spacing.xs,
  '--toto-spacing-sm': spacing.sm,
  '--toto-spacing-md': spacing.md,
  '--toto-spacing-lg': spacing.lg,
  '--toto-spacing-xl': spacing.xl,
  '--toto-spacing-xxs': spacing.xxs,
} as const;

export type SpacingToken = keyof typeof spacing;
export type SemanticSpacing = keyof typeof semanticSpacing;
